@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:'Montserrat', 'sans-serif', 'arial';background-color:white}.banner{height:500px;background-position:center;background-size:cover;color:white;display:flex;justify-content:center;align-items:center}.banner h1{font-size:100px;font-weight:600}

.Filterbar-container .Filterbar-sort-options{background-color:#dfe6e9;padding:20px;display:flex;margin-bottom:50px;justify-content:flex-end;align-items:center}.Filterbar-container .Filterbar-sort-options p{margin-bottom:0;margin-right:10px}.Filterbar-container .Filterbar-sort-options select{width:200px}@media screen and (max-width: 350px){.Filterbar-container .Filterbar-sort-options select{width:150px}}

.Beercard-container{padding:20px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);-webkit-transition:all ease 0.3s;transition:all ease 0.3s;height:432px;background-color:white;border-radius:5px;color:#212025}.Beercard-container:hover{box-shadow:0 8px 16px 0 rgba(0,0,0,0.2)}.Beercard-container img{max-height:200px}.Beercard-container .Beercard-name{height:56px}.Beercard-container .Beercard-abv{font-weight:800}

